


















































import { Component, Vue, Prop } from "vue-property-decorator";
import {AuthModule} from "../store/modules/auth";
import {NavigatorModule} from "../store/modules/navigator";
import HomePage from "./home.vue";
import AppSplitter from "../app-splitter.vue";
import {MemorandumTagModule} from "../store/modules/memorandum-tag";
import { ResultCodeEnum } from '../core/enums/result-code.enum';
import {UserModule} from '../store/modules/user';
import { RouterUtils } from '../utils/router.utils';

@Component
export default class PersonSetting extends Vue {
    @Prop() toolbarInfo?: {};
    get loginState() {
        return AuthModule.token;
    }

    async logout() {
        this.$ons.notification
            .confirm("", {
                buttonLabels: ["", ""],
                title: ""
            })
            .then(code => {
                // console.log(code.nodeValue);
                if (code == 0) {
                    AuthModule.logout();
                    this.$ons.notification.toast("", {
                        buttonLabels: "",
                        timeout: 1500
                    });
                    NavigatorModule.reset({
                        extends: AppSplitter
                    });
                }
            });
    }
    async refreshCache() {
        await UserModule.getCurrentInfo();
        MemorandumTagModule
            .getTagList()
            .then(code => {
                if (code == ResultCodeEnum.SUCCESS) {
                    this.$ons.notification.toast("",{buttonLabels:[''],timeout:1500});
                }
            });
    }

    async feedback(){
        await this.$ons.notification.alert("wx:FunTrybe",{
            title: "",
            buttonLabels: ""
        });
    }
}
